@import '../../css/variables';

.header-container {
  padding: 10px 20px;
  background-color: $COLOR-SECONDARY;

  display: flex;
  flex-direction: column;
}

.nav-bar {
  display: flex;
  align-items: center;
  flex-direction: row;

  @media only screen and (max-width: 520px) {
    flex-direction: column;
    align-items: flex-start;
  }

  .logo-container {
    height: 2rem;
    width: 2rem;
    background-image:url(../../assets/icons/zlogo.png);
    background-position: center;
    background-size: cover;

    @media only screen and (max-width: 550px) {
      display: none;
    }
  }

  nav {
    ul {
      padding: 0 10px;
      list-style-type: none;
      display: flex;
      gap: 5px;
      color: black;

      a {
        text-decoration: none;
        padding: 0 5px;
        color: black;
        &:hover {
          background-color: $COLOR-SECONDARY-DARK;
        }
        &:visited {
          color: black;
        }
      }
    }
  }

  .active {
    background-color: $COLOR-PRIMARY;
  }

  .search-bar {
    margin-left: auto;
    padding: 0 20px;

    @media only screen and (max-width: 520px) {
      margin: 0;
      padding: 0;
    }
  }
}

.page-address {
  padding-top: 10px;
  font-weight: bold;
  font-size: 0.8rem;
  &::before {
    font-weight: 400;
    content: "Page address: ";
  }
}
