@import '../../css/variables';

.card-container {
  margin: 0;
  padding: 10px;
  border: 1px solid $COLOR-PRIMARY-DARK;
  width: $CARD-WIDTH;
  height: 450px;
  display: flex;
  flex-direction: column;
  box-shadow: 4px 4px 10px $COLOR-SECONDARY-DARK;
  border-radius: 5px;
  background-color: white;
  overflow: hidden;

  &:hover {
    box-shadow: none;
    cursor: pointer;
    background-color: $COLOR-PRIMARY-LIGHT;
  }

  .image-container {
    .preview-img {
    width: calc($CARD-WIDTH - 20px);
    border-radius: 5px;

    }
  }

  .product-info {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding-bottom: 5px;
    // border: 1px solid rgb(30, 255, 0);

    .product-brand {
      font-weight: bold;
      text-transform: uppercase;
      color: $COLOR-SECONDARY-DARK;
    }

    .product-name {
      font-family: 'Courier New', Courier, monospace;
      height: 2.5rem;
    }

    .product-description {
      font-size: small;
      border-top: 1px dotted lightgray;
      border-bottom: 1px dotted lightgray;
      height: 3.5rem;
    }

    .product-price {
      font-weight: bold;
      margin-top: auto;
      align-self: center;
      &::after {
        content: " USD";
      }
    }
  }

  .product-features {
    .feat-steel,
    .feat-blade-len,
    .feat-weight {
      font-size: small;
    }
    .feat-steel::before {
      content: "Blade Steel: ";
      color: $COLOR-SECONDARY-DARK;
      padding-left: 5px;
    }
    .feat-blade-len::before {
      content: "Blade Length: ";
      color: $COLOR-SECONDARY-DARK;
      padding-left: 5px;
    }
    .feat-weight::before {
      content: "Weight: ";
      color: $COLOR-SECONDARY-DARK;
      padding-left: 5px;
    }
    .feat-blade-len::after {
      content: "mm";
      }
    .feat-weight::after {
      content: "g";
    }
  }

  .product-actions {
    display: flex;
    flex-direction: row;
    width: $CARD-WIDTH;
    margin-left: -10px;
    margin-bottom: -10px;
    padding: 0 15px 0 0;
    align-items: center;
    background-color: lightgray;
    height: 2.5rem;

    .btn-fav {
      width: 40px;
      height: 1.5rem;
      border: none;
      background-color: transparent;

      &:hover {
        cursor: pointer;
        .btn-icon {
          height: 1.2rem;
        }
      }

      .btn-icon {
        height: 1rem;
        width: auto;
        align-self: center;
      }
    }

    .btn-addToCart {
      margin-left: auto;
      display: flex;
      width: 40px;
      height: 1.5rem;
      padding: 3px 5px;
      justify-content: center;

      &:hover {
        cursor: pointer;
      }

      .btn-icon {
        height: 1rem;
        width: auto;
        align-self: center;
      }
    }
  }

}
